	module.exports = {"data":{"markdownRemark":{"html":"<pre><code class=\"language-HTML\">&#x3C;!DOCTYPE html PUBLIC \"-//W3C//DTD HTML 3.2 Final//EN\"> \n &#x3C;html> \n  &#x3C;head> \n    &#x3C;meta content=\"text/html; charset=SHIFT_JIS\" http-equiv=\"Content-Type\"> \n    &#x3C;title>もじもじのホームページ&#x3C;/title> \n  &#x3C;/head> \n  &#x3C;body> \n    &#x3C;p>もじもじ文字&#x3C;/p> \n  &#x3C;/body> \n &#x3C;/html> \n</code></pre>\n<h2>Commentary</h2>\n<p>A new tag <code>&#x3C;head></code> came out. You can tinker with the setting of pages here.</p>\n<p><code>&#x3C;meta></code> . This is a very important tag, but it is difficult at this stage, so it is advisable to copy it as it is. If you copy it, if you <code>SHIFT_JIS</code> garbled, <code>SHIFT_JIS</code> with <code>EUC-JP</code> or <code>iso-2022-jp</code> or <code>utf-8</code> .</p>\n<p><code>&#x3C;title></code> part surrounded by <code>&#x3C;title></code> is the title of that page. For example, in the above example, it is \"a page of mackerel.\"</p>\n<p>The rest is the same as last time.</p>","frontmatter":{"cover":null,"date":"2001-09-21T15:34:00+09:00","strDate":"2001/9/21","title":"HTML Course 2"}}},"pathContext":{"path":"/en/blog/2001/09/21-html%e8%ac%9b%e5%ba%a72/","langKey":"en"}}

